@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)),
    linear-gradient(0deg, #080A2F, #080A2F);

}

.App {
  text-align: center;
  width: 393px;
  height: 500px;
  border: 1px solid black;
  margin: 50px auto;
  color: white;
  font-family: 'Poppins';
  background-image: url('https://i08.fotocdn.net/s111/320ba2feabaae867/public_pin_l/2496085574.jpg');
  border-radius: 25px;
  background-size: no-repeat;
  background-position: center;
  background-size: cover;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 15px 16px 0 16px;
}

.ideolocator>img {
  width: 100%;
  height: 100%;
}

.ideolocator {
  width: 32px;
  height: 32px;
}

.cityName {
  margin-left: 2px;
  margin-right: auto;
  font-size: 18px;
  font-weight: 300;
  line-height: 16px;
  letter-spacing: 0em;
  text-align: center;
}

.searchInput {
  background: transparent;
  border: 1px solid white;
  border-radius: 25px;
  color: white;
  padding-left: 5px;
}

.searchInput::placeholder {
  color: white;
  border-style: none;
}

.searchInput:focus::placeholder {
  color: transparent;
}

.searchInput:focus {
  outline: none;
}

.realTime {
  margin: 45px 40px 50px;
}

.dateBlock {
  font-size: 24px;
  font-weight: 700;
  line-height: 36px;
  letter-spacing: 0px;
}

.timeBlock {
  font-size: 14px;
  font-weight: 300;
  line-height: 21px;
  letter-spacing: 0px;
}

.weatherDescription {
  margin: 0 50px;
  font-size: 36px;
  font-weight: 700;
  line-height: 54px;
  letter-spacing: 0px;
}

.weatherDescription::first-letter {
  text-transform: uppercase;
}

.temperatureContainer {
  display: flex;
  justify-content: center;
  margin: auto;
}

.temperature {
  width: 128px;
  height: 100px;
}

.temperature p {
  width: 128px;
  height: 72px;
  font-size: 86px;
  line-height: 72px;
  text-align: left;
}

.temperature span {
  line-height: 28px;
  padding-left: 35%;
}

.infoWeather {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 50px 22px 25px;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0px;
  text-align: center;
}

.infoWeather img {
  width: 30px;
  height: 30px;
}

.wind span {
  text-transform: none;
}

.feelsLike {}

.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  height: 100%;
}